import { Avatar, Box, Card, CardContent, Grid, Typography } from '@mui/material';
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';
import MoneyIcon from '@mui/icons-material/Money';
import { useSelector } from 'react-redux';
import React, { useEffect } from 'react';

export const Budget = (props) =>{ 
  const astroTrans=useSelector(state=>state.astrotrans)?.list
  const [totalEarned,setTotalEarned]=React.useState(0)

  useEffect(()=>{
      if(astroTrans){
        let total=0
        for(let i of astroTrans){
          total+=Math.abs(Number(i?.amount))
        }
        setTotalEarned(total)
      }
  },[astroTrans])

  return(
  <Card
    sx={{ height: '100%' }}
    {...props}
  >
    <CardContent>
      <Grid
        container
        spacing={3}
        sx={{ justifyContent: 'space-between' }}
      >
        <Grid item>
          <Typography
            color="textSecondary"
            gutterBottom
            variant="overline"
          >
            Total Earned
          </Typography>
          <Typography
            color="textPrimary"
            variant="h4"
          >
            {totalEarned}
          </Typography>
        </Grid>
        <Grid item>
          <Avatar
            sx={{
              backgroundColor: 'error.main',
              height: 56,
              width: 56
            }}
          >
            <MoneyIcon />
          </Avatar>
        </Grid>
      </Grid>
      <Box
        sx={{
          pt: 2,
          display: 'flex',
          alignItems: 'center'
        }}
      >
        {/* <ArrowDownwardIcon color="error" />
        <Typography
          color="error"
          sx={{
            mr: 1
          }}
          variant="body2"
        >
          12%
        </Typography>
        <Typography
          color="textSecondary"
          variant="caption"
        >
          Since last month
        </Typography> */}
      </Box>
    </CardContent>
  </Card>
);}
