import React, { useEffect } from 'react'
import { Box, Typography, Button, TextField, Input } from '@mui/material'
import { ScreenContainer } from 'components'
import { useDispatch, useSelector } from 'react-redux'
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import CheckoutForm from "./CheckoutForm";
import hit from 'service';
import Loader from '../../../components/loader'
import { setUserData } from 'redux_store/features/Auth';
const stripePromise = loadStripe("pk_test_ZnW7YROAHUh2eKXFlbRyxljC00ZIVjxiHS");

export default function AddMoneyToWallet() {
    const user = useSelector(state => state?.auth)?.user
    const [money, setMoney] = React.useState(0)
    const [clientSecret, setClientSecret] = React.useState("");
    const [loading,setLoading]=React.useState(false)
    const dispatch=useDispatch()
    
    useEffect(()=>{
        getUserDetails()
    },[])

    const appearance = {
        theme: 'stripe',
    };
    const options = {
        clientSecret,
        appearance,
    };
    const getSecretEky = async () => {
        try{
            setLoading(true)
            let res = await hit("/user/generateSecretKey", "post", {
                "amount": money,
                "customer_id": user?.account_id,
                "description": "postman"
            })
          
            if (!res.err) {
                setClientSecret(res.data)
            }
        }catch(err){

        }finally{
            setLoading(false)
        }
    }
    const getUserDetails=async()=>{
        try{
            setLoading(true)
            let res = await hit("/user/getProfile", "post", {})
            if (!res.err) {
                dispatch(setUserData({user:res.data}))
                
            }
        }catch(err){

        }finally{
            
            setLoading(false)
        } 
    }
    const addTransaction=async()=>{
        try{
            setLoading(true)
            let res = await hit("/user/addTransaction", "post", {
                "amount": money,
                "description": `Added to wallet`
            })
            if (!res.err) {
                window.location.reload ()
                getUserDetails()
            }
        }catch(err){

        }finally{
            setLoading(false)
        }
    }
    return (
        <ScreenContainer style={{ paddingX: { "mobile": "5%", laptop: "10%", minHeight: "50vh" } }} >
            <Typography textAlign="center" fontSize="1.5em"> Add Money to Wallet </Typography>
            <Typography textAlign="center" color="secondary.main" my={"1em"}>Available Balance : {user?.balance} </Typography>
            
            <Box display="flex" justifyContent="center">
                <TextField
                    name="phone"
                    type="number"
                    value={money}
                    onChange={e => {
                        setMoney(e.target.value)
                    }}
                    placeholder="Enter amount in "
                    id="phone"
                    sx={{
                        width: { mobile: "80%", laptop: "25%" },
                        "& label.Mui-focused": {
                            color: "secondary.main"
                        }
                        , "& .MuiOutlinedInput-root": {
                            "&.Mui-focused fieldset": {
                                borderColor: "secondary.main"
                            }
                        }
                    }
                    }
                />
                <Button sx={{ marginLeft: "1em" }} onClick={getSecretEky} variant="contained">Proceed</Button>
            </Box>
            <Box display="flex" flexWrap="wrap" alignSelf="center" marginTop={"1em"} justifyContent="space-evenly">
            {clientSecret && (
                <Elements options={options} stripe={stripePromise}>
                    <CheckoutForm setClientSecret={setClientSecret} addTransaction={addTransaction} />
                </Elements>
            )}
            </Box>
            <Box display="flex" flexWrap="wrap" alignSelf="center" justifyContent="space-evenly">
                { [5*82, 10*82, 15*82,20*82,25*82, 30*82,35*82,40*82,45*82, 50*82].map(x => {
                    return (
                        <Box onClick={() => {
                            setMoney(x)
                        }} sx={{
                            border: "1px solid gray",
                            borderLeft: "4px solid gold",
                            borderRight: "4px solid gold",
                            padding: "2em",
                            borderRadius: "40px",
                            marginTop: "1em",
                            justifyContent: "center",
                            "&:hover": {
                                boxShadow: "2px 2px 5px gray",
                                cursor: "pointer",
                            },
                            width: { mobile: "30%", laptop: "18%" }
                        }}>
                            <Typography fontSize="2em" textAlign="center">
                            {x}
                            </Typography>
                        </Box>
                    )
                })}
            </Box>
            {loading&&<Loader />}
        </ScreenContainer>
    )
}